.nav-toggle {
  position: relative;
  z-index: 300;
  width: 30px;
  height: 20px;
  box-shadow: 0 4px #333 inset;
  cursor: pointer;
}

.nav-toggle::after, .nav-toggle::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 4px;
  background-color: #333;
  bottom: 0;
}

.nav-toggle::after {
  bottom: 8px;
}

/*
| Mixin para crear cuadriculas
|
| $gridItemSelector: nombre del selector CSS de cada item de la cuadricula
| $listColumns:
|              * lista de numeros de columnas en cada breakpoint (separados por espacios). Ej: 1 2 3 4
|              * el numero de columnas del ultimo breakpoint se hereda a breakpoints superiores
| $gutter: separacin entre cada item de la cuadricula (opcional)
|
*/
/* Mixins de alineacion con compatibilidad hacia atras*/
:root {
  --color: #007BDF;
  --color-alt: #006DC6;
  --border-color: #DFE0E0;
  --light-bg: #F0F0F0;
  --border-radius: 0.25rem;
  --vertical-block-space: 1rem;
  --vertical-content-space: 1rem;
  --gap: 1rem;
  --max-width: 1200px;
}

@media screen and (min-width: 1280px) {
  :root {
    --vertical-block-space: 2rem;
    --gap: 2rem;
  }
}

:root {
  --header-height: 1rem;
  --font-size-title: .6em;
  --font-size-content: 1em;
  --font-size-sub-content: 1em;
  --font-size-date: .6em;
  --color-border-card: 1px solid #323435;
  --color-border-card-hover: 1px solid #FFFFFF;
  --color-text-default: #FFFFFF;
  --color-text-title: #FFFFFF;
  --color-text-content: #FFFFFF;
  --color-text-sub-content: #A4A6A8;
  --color-text-date: #FFFFFF;
  --color-body-bg: #262728;
}

@media screen and (min-width: 960px) {
  :root {
    --header-height: 2rem;
    --font-size-content: 1.4em;
    --font-size-sub-content: .8em;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  min-height: calc(100vh - var(--header-height));
  margin: var(--header-height) 0 0 0;
  background: var(--color-body-bg);
  color: var(--color-text-default);
  font-family: 'PT Sans', sans-serif;
}

.main-header {
  margin: 0 0 50px 0;
}

.main-header .btn-add {
  margin-left: auto;
  font-family: 'PT Sans', sans-serif;
  font-weight: bold;
  border-radius: 5px;
  color: #707070;
  padding: 0 10px;
  font-size: 12px;
}

@media screen and (min-width: 0) and (max-width: 1599px) {
  .main-header {
    padding: 0 15px;
  }
}

.card {
  position: relative;
  border: var(--color-border-card);
  padding: 40px;
  border-radius: 5px;
}

.card__close {
  position: absolute;
  padding: .3em .6em;
  background: #262728;
  border-radius: 50%;
  border: 1px solid white;
  color: white;
  top: -15px;
  right: -16px;
  cursor: pointer;
}

.card .card__title, .card .card__content {
  font-weight: bold;
}

.card__title {
  font-size: var(--font-size-title);
  color: var(--color-text-title);
}

.card__content {
  font-size: var(--font-size-content);
  color: var(--color-text-content);
}

.card__sub-content {
  font-size: var(--font-size-sub-content);
  color: var(--color-text-sub-content);
}

.card__date {
  font-size: var(--font-size-date);
  color: var(--color-text-date);
}

.card:hover {
  border: var(--color-border-card-hover);
}

.card:hover .card__close {
  background: white;
  color: #262728;
}
